<template>
  <div>
    <el-row class="filter-query">
      <el-col :span="4">
        <span class="filter-span">组织名称</span>
      </el-col>
      <el-col :span="12" :offset="1">
        <el-input placeholder="组织机构" v-model="organizationFilter"></el-input>
      </el-col>
      <el-col :span="6" :offset="1" class="filter-btn">
        <el-button type="primary" @click="(event) => add(event)" size="medium">新增</el-button>
      </el-col>
    </el-row>
    <el-tree :ref="ref" :data="tree.data" node-key="orgId" accordion
             :lazy="true"
             :load="asyncLoadTreeNode"
             :show-checkbox="checkbox"
             :highlight-current="true"
             :expand-on-click-node="false"
             :filter-node-method="filterNode"
             :props="tree.defaultProps"
             :default-expanded-keys="expandedList"
             @node-collapse="nodeCollapse"
             @node-expand="nodeExpand"
             @node-click="clickNode">
    <span class="custom-tree-node" slot-scope="{ node, data }">
      <span>{{ data.orgName }}</span>
      <span v-if="showActionButton">
        <el-button type="text" size="mini"
                   @click="(event) => add(event, data, node)">新增</el-button>
        <el-button type="text" size="mini"
                   @click="(event) => edit(event, data, node)">修改</el-button>
        <el-button type="text" size="mini"
                   @click="(event) => remove(event, data, node)">删除</el-button>
      </span>
    </span>
    </el-tree>
  </div>
</template>

<script>
  export default {
    name: "OrganizationTree",
    props: {
      checkbox: {
        type: Boolean,
        require: false,
        default: false
      },
      showActionButton: {
        type: Boolean,
        require: false,
        default: false
      },
      rootId: {
        type: Boolean,
        require: false,
        default: false
      }
    },
    data() {
      return {
        ref: 'tree',
        organizationFilter: '',
        expandedList: [],
        tree: {
          data: [],
          defaultProps: {
            isLeaf: 'leaf'
          }
        }
      }
    },
    created() {
      this.initTreeNodes();
    },
    watch: {
      'organizationFilter'(newV, oldV) {
        this.$refs[this.ref].filter(newV)
      }
    },
    methods: {
      async asyncLoadTreeNode(node, resolve) {
        if(node.level !== 0){
          let nodes = [];
          let result = await this.$api.organizations.getOrganizationList(node.data.orgId);
          result.data.data && result.data.data.forEach( node => {
            nodes.push({
              orgId: node.orgId,
              orgCode: node.orgCode,
              orgName: node.orgName,
              parentOrgId: node.parentOrgId,
              path: node.path,
              leaf: node.childCount === 0
            })
          });
          resolve(nodes);
        }
      },
      async initTreeNodes() {
        this.tree.data = [];
        let res = await this.$api.organizations.getOrganizationList('-1');
        res.data.data && res.data.data.forEach( node => {
          this.tree.data.push({
            orgId: node.orgId,
            orgCode: node.orgCode,
            orgName: node.orgName,
            parentOrgId: node.parentOrgId,
            path: node.path,
            leaf: node.childCount === 0
          })
        })
      },
      add(event, data, node) {
        this.$emit('add', event, data, node);
      },
      edit(event, data, node) {
        this.$emit('edit', event, data, node);
      },
      remove(event, data, node) {
        this.$emit('remove', event, data, node);
      },
      clickNode(data, node, self) {
        this.$emit('clickNode', data, node, self);
      },
      filterNode(value, data) {
        if (!value) {
          return true
        }
        return data.orgName.indexOf(value) !== -1
      },
      nodeExpand(data) {
        this.expandedList.push(data.orgId); // 在节点展开是添加到默认展开数组
      },
      nodeCollapse(data) {
        this.expandedList.splice(this.expandedList.indexOf(data.orgId), 1); // 收起时删除数组里对应选项
      },
      getCheckedNodes() {
        return this.$refs[this.ref].getCheckedNodes();
      },
      getElementTreeRef() {
        return this.$refs[this.ref];
      },
    }
  }
</script>

<style>
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 12px;
  }
  
  .filter-query {
    margin-bottom: 20px;
  }
  
  .filter-query .filter-span {
    height: 40px;
    line-height: 40px;
  }
  
  .filter-query .filter-btn {
    height: 40px;
    line-height: 40px;
  }
</style>