<template>
  <el-container id="organization" style="margin-top: -10px">
    <el-main>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-card shadow="always">
            <div slot="header">
              <span>组织机构管理</span>
            </div>
            <organization-tree
                :showActionButton="true"
                :ref="refs.tree"
                :checkbox="false"
                @clickNode="clickNode"
                @add="add"
                @edit="edit"
                @remove="remove">
            </organization-tree>
          </el-card>
        </el-col>
        <el-col :span="14">
          <organization-form
              :ref="refs.form"
              :content="form.content"
              :action="form.action"
              :disabled="form.disabled"
              :show.sync="form.show"
              @refreshTree="refreshTreeNode"
              @afterAction="afterSubmit">
          </organization-form>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
import OrganizationForm from '@components/organizations/OrganizationForm';
import OrganizationTree from '@components/organizations/OrganizationTree';

export default {
  name: 'OrganizationSection',
  components: {
    'organization-form': OrganizationForm,
    'organization-tree': OrganizationTree,
  },
  data() {
    return {
      refs: {
        tree: 'tree',
        form: 'form'
      },
      form: {
        content: {},
        show: false,
        disabled: true,
        action: 'detail'
      }
    };
  },
  methods: {
    add(event, data, node) {
      event.stopPropagation();
      /* let path = [];
      if(node){
        let item = node.parent;
        for (let i = 0; i < node.level - 1; i++){
          path.unshift(item.data.orgId);
          item = item.parent;
        }
      }*/
      this.form.show = true;
      this.form.disabled = false;
      this.form.action = 'add';
      this.form.content = {
        parentOrgId: node ? data.orgId : '-1',
        path: node ? data.path : '',
      };
      /* path.length > 0
        ? this.form.content.path = path.join(',')
        : null*/
    },
    edit(event, data, node) {
      event.stopPropagation();
      /* let path = [];
      let item = node;
      for (let i = 0; i < node.level; i++){
        path.unshift(item.data.orgId);
        item = item.parent;
      }*/
      this.form.show = true;
      this.form.disabled = false;
      this.form.action = 'edit';
      this.form.content = {
        parentOrgId: data.parentOrgId,
        orgId: data.orgId,
        orgName: data.orgName,
        orgCode: data.orgCode,
        path: data.path,
      };
      /* path.length > 0
        ? this.form.content.path = path.join(',')
        : null*/
    },
    async remove(event, data, node) {
      event.stopPropagation();
      if (!node.isLeaf) {
        this.$message({
          showClose: true,
          message: '该节点下存在子节点，不允许删除！',
          type: 'error'
        });
        return;
      }

      this.$confirm(`此操作将删除资源【${data.orgName}】，是否继续？`, '删除', {
        type: 'warning'
      }).then( async () => {
        let result = await this.$api.organizations.deleteOrganization(data.orgId);
        this.$message({
          showClose: true,
          message: result.data.meta.message,
          type: result.data.meta.success ? 'success' : 'error'
        });
        this.form.show = false;
        this.refreshTreeNode( data.parentOrgId, true);
      });
    },
    clickNode(data, node, self) {
      this.form.content = {
        parentOrgId: data.parentOrgId,
        orgId: data.orgId,
        orgName: data.orgName,
        orgCode: data.orgCode,
      };
      this.form.show = true;
      this.form.disabled = true;
      this.form.action = 'detail';
    },
    async refreshTreeNode(parentOrgId, disabled) {
      this.form.disabled = disabled;
      let result = await this.$api.organizations.getOrganizationList(parentOrgId);
      let nodes = [];
      result.data.data && result.data.data.forEach( (node) => {
        nodes.push({
          orgId: node.orgId,
          orgCode: node.orgCode,
          orgName: node.orgName,
          parentOrgId: node.parentOrgId,
          path: node.path,
          leaf: node.childCount === 0
        });
      });
      if (parentOrgId == -1) {
        this.$refs[this.refs.tree].initTreeNodes();
        return 1;
      }
      let nodeId = parentOrgId;
      let node = this.$refs[this.refs.tree].getElementTreeRef().getNode(nodeId);
      node.expand();
      this.$refs[this.refs.tree].getElementTreeRef().updateKeyChildren(parentOrgId,
        nodes);
      return 1;
    },
    afterSubmit() {

    },
  }
};
</script>

