<template>
  <el-card shadow="always" v-show="show === true">
    <div slot="header">
      <span>组织信息</span>
    </div>
    <el-form :ref="ref" :model="content" :rules="rules" label-width="100px" :disabled="disabled">
      <el-form-item label="组织编码" prop="orgCode">
        <el-input v-model="content.orgCode" :disabled="disabled"></el-input>
      </el-form-item>
      <el-form-item label="组织名称" prop="orgName">
        <el-input v-model="content.orgName" :disabled="disabled"></el-input>
      </el-form-item>
      <!--<el-form-item label="组织级别">
        <el-select :model="content">
          <el-option v-for="item in selects" :value="item.value" :label="item.name" :key="item.value">
          </el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  name: "OrganizationForm",
  props: ['content', 'show', 'action', 'disabled'],
  data() {
    return {
      ref: 'form',
      selects: [],
      rules: {
        orgCode: [{required: true, message: '请输入组织编码', trigger: 'blur'}],
        orgName: [{required: true, message: '请输入组织名称', trigger: 'blur'}]
      }
    }
  },
  created() {
  
  },
  watch: {
    'action'(newV, oldV) {
      this.$refs.form.resetFields();
    }
  },
  methods: {
    cancelAction() {
      this.$emit('update:show', false);
    },
    onSubmit() {
      this.$refs[this.ref].validate(async (valid) => {
        if(valid){
          let result = await (this.action == 'add'
            ? this.$api.organizations.addOrganization(this.content)
            : this.action == 'edit'
              ? this.$api.organizations.editOrganization(this.content)
              : null);
          if(result.data.meta && result.data.meta.success){
            this.$message({
              showClose: true,
              message: result.data.meta.message,
              type: 'success'
            });
            this.$emit('refreshTree',this.content.parentOrgId, true);
          }else {
            this.$message({
              dangerouslyUseHTMLString: true,
              showClose: true,
              message: result.data.meta.message,
              type: 'error'
            });
          }
        }
      })
    }
  }
}
</script>

